.tab-buttons {
    display: flex;
    background-color: #f1f1f1;
    border-bottom: 1px solid #ccc;
  }
  
  .tab-button {
    padding: 10px 20px;
    font-size: 16px;
    border: none;
    background-color: transparent;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .tab-button.active {
    background-color: #fff;
  }
  
  .tab-content {
    height:auto;
    padding: 20px;
    background-color: #f9f9f9;
    border: 1px solid #ccc;
  }